import * as React from 'react';
import styled from 'styled-components';
import { DeleteBetaNormal, Tooltip } from '@ali/we-design';

const Button = styled.span`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(17,31,44,0.72);
  margin: 0;
  padding: 0;
  width: 28px;
  height: 28px;
  font-size: 16px;
  cursor: pointer;
  line-height: 16px;
  border-radius: 4px;

  &:hover {
    background-color: rgba(17,31,44,0.08);
  }
`;

interface ToolbarMenuProps {
  mode: string;
  style?: React.CSSProperties;
  onMenuEvent: (command: string) => void;
  setIsHighlightSelection: (isHighlight: boolean) => void;
  locale?: Record<string, any>;
}

export default class ToolbarMenu extends React.Component<ToolbarMenuProps> {
  handleMouseDown = (e) => {
    e.preventDefault(); // Prevent blur.
    e.stopPropagation();
    const { onMenuEvent } = this.props;
    onMenuEvent('delete-table');
  };

  highlightSelection = () => {
    const { setIsHighlightSelection } = this.props;
    setIsHighlightSelection(true);
  };

  cancelHighlightSelection = () => {
    const { setIsHighlightSelection } = this.props;
    setIsHighlightSelection(false);
  };

  render() {
    const { style, locale = {} } = this.props;
    return (
      <Button
        style={style}
        onMouseDown={this.handleMouseDown}
        onTouchEnd={this.handleMouseDown}
        onMouseOver={this.highlightSelection}
        onMouseLeave={this.cancelHighlightSelection}
      >
        <Tooltip placement="top" title={locale.contextMenuDeleteTable}>
          <span>
            <DeleteBetaNormal />
          </span>
        </Tooltip>
      </Button>
    );
  }
}
